<template>
  <div :id="eleId" class="echart-unit">组件</div>
</template>

<script>
  import * as echarts from 'echarts';
  const defaultEchdata = [
    { name: '16-20岁', value: 31165 },
    { name: '21-30岁', value: 40084 },
    { name: '31-45岁', value: 132793 },
    { name: '46-60岁', value: 36283 },
    { name: '60岁以上', value: 23645 }
  ];
  const defaultTitle = '标题';
  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: defaultTitle
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        let echartEle = echarts.init(document.getElementById(this.eleId));
        var option = {
          title: {
            text: '饼图示例',
            subtext: '2023年6月8日',
            left: 'center',
            textStyle: {
              color: '#fff',
              fontSize: '13PX',
              fontWeight: 400
            }
          },
          tooltip: {
            trigger: 'item'
          },
          //   legend: {
          //     orient: 'vertical',
          //     left: 10,
          //     data: ['A', 'B', 'C', 'D', 'E']
          //   },
          grid: [
            {
              left: '20',
              right: '20',
              bottom: '10',
              top: '10',
              containLabel: true
            }
          ],
          series: [
            {
              name: '',
              type: 'pie',
              radius: ['30%', '60%'],
              center: ['50%', '55%'],
              // startAngle: 40,
              hoverAnimation: false,
              color: ['#ECC743', '#97D9E1', '#27D3FF', '#9599E2', '#62AFFD', '#28B61E'],
              label: {
                show: true,
                // alignTo: 'edge',
                // formatter: '{name|{b}}\n{num|{c}%}',
                // formatter: '{name|{b}}\n{num|{c}}',
                formatter: '{name|{b}}\n{ng|({d}%)}',
                minMargin: 5,
                edgeDistance: 15,
                lineHeight: 20,
                rich: {
                  name: {
                    fontSize: 12,
                    color: '#fff'
                  },
                  num: {
                    fontSize: 12,
                    fontWeight: '100',
                    color: '#447EBE'
                  },
                  ng: {
                    fontSize: 13,
                    fontWeight: '600',
                    color: '#ffde00'
                  }
                }
              },
              labelLine: {
                length: 20,
                length2: 30,
                maxSurfaceAngle: 80
              },
              itemStyle: {
                normal: {
                  // borderColor: '#2b2a38',
                  // borderWidth: 2
                }
              },
              data: this.echdata
            }
          ]
        };
        echartEle.setOption(option);
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
