<template>
  <div :id="eleId" class="echart-unit">组件</div>
</template>

<script>
  import * as echarts from 'echarts';

  const defaultEchdata = {
    xData: [12, 310, 348],
    yData: ['省外', '省内', '市内']
  };

  export default {
    props: {
      echdata: {
        type: [Object, Array],
        default: () => defaultEchdata
      },
      titleText: {
        type: String,
        default: '标题666'
      }
    },
    data() {
      return {
        eleId: `eleId_${Math.random()
          .toString()
          .substring(3, 6) + Date.now().toString(36)}`,
        chart: null
      };
    },
    mounted() {
      this.$echarts = echarts;
      this.$nextTick(() => {
        this.initEchart();
      });
    },
    methods: {
      initEchart() {
        var data1 = this.echdata.xData;
        // console.log('data1', data1);
        // var barMax = Math.max(...data1) + 1000;
        // var dataTop = [];
        // for (var i = 0; i < data1.length; i++) {
        //   dataTop.push(barMax);
        // }
        const arrData = [
          {
            // 柱体1
            name: '数量',
            data: data1,
            type: 'bar',
            barMaxWidth: 'auto',
            barWidth: '10',
            showBackground: true,
            backgroundStyle: {
              color: 'rgba(20, 88, 168, 0.2)'
            },
            itemStyle: {
              normal: {
                color: params => {
                  var colorList = [['#2D98DF', '#0B244C']];
                  // var index = params.dataIndex
                  // if (params.dataIndex >= colorList.length) {
                  //   index = params.dataIndex - colorList.length
                  // }
                  return new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    {
                      offset: 0,
                      color: colorList[0][0]
                    },
                    {
                      offset: 0.5,
                      color: colorList[0][0]
                    },
                    {
                      offset: 1,
                      color: colorList[0][1]
                    }
                  ]);
                },
                label: {
                  show: true,
                  position: 'top',
                  textStyle: {
                    fontSize: 14,
                    fontWeight: 600,
                    color: '#D1DBFF' // 字体颜色
                  }
                }
              },
              barBorderRadius: 30
            },
            zlevel: 2
          }
        ];
        const option = {
          title: {
            text: this.titleText,
            top: -2,
            textStyle: {
              color: '#fff', // 字体颜色
              fontSize: '14px', // 字体大小
              fontWeight: 600 // 字体粗细
            }
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            right: '5%',
            left: '5',
            bottom: '0',
            top: '30',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: this.echdata.yData,
            // 坐标轴
            axisLine: {
              show: true,
              lineStyle: {
                color: '#1d71b7'
              }
            },
            // 坐标值标注
            axisLabel: {
              show: true,
              textStyle: {
                color: '#96C2FF',
                // color: this.$store.state.app.echartStyle.titleColor, // 字体颜色
                fontSize: 12
              }
            },
            axisTick: {
              show: false
            }
          },
          yAxis: {
            // 坐标轴
            axisLine: {
              show: true,
              lineStyle: {
                color: '#1d71b7'
              }
            },
            // 坐标值标注
            axisLabel: {
              show: true,
              textStyle: {
                color: '#96C2FF',
                // color: this.$store.state.app.echartStyle.titleColor, // 字体颜色
                fontSize: 12
              }
            },
            // 分格线
            splitLine: {
              show: false,
              lineStyle: {
                color: '#4784e8'
              }
            }
          },
          series: arrData
        };
        //
        let echartEle = echarts.init(document.getElementById(this.eleId));
        echartEle.setOption(option);
        window.addEventListener('resize', () => {
          echartEle.resize();
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  .echart-unit {
    height: 100%;
  }
</style>
